nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #27375e;
  padding: 20px 80px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.06);
}
#navbar {
  display: flex;
  align-items: center;
  justify-content: center;
}
#navbar li {
  list-style: none;
  padding: 0 20px;
  position: relative;
}
#navbar li a {
  text-decoration: none;
  font-size: 1.3em;
  font-weight: 600;
  color: #fff;
  transition: 0.3s ease-in-out;
}
#navbar li a:hover,
#navbar li a.active {
  color: #f46a16;
}
#navbar li a:hover::after,
#navbar li a.active::after {
  content: "";
  width: 30%;
  height: 2px;
  background-color: #f46a16;
  position: absolute;
  bottom: -4px;
  left: 20px;
}

#mobile i {
  color: #fff;
}
@media screen and (max-width: 769px) {
  #navbar {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    top: 90px;
    right: 0px;
    width: 300px;
    height: 100vh;
    background-color: #7e87b4;
    box-shadow: 0 40px 60px rgba(0, 0, 0, 0.01);
    padding: 40px 0 0 10px;
  }
  #mobile i {
    display: block;
  }
}
